.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.profile-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.profile-picture-container {
  position: relative;
  margin-right: 20px;
}

.profile-avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: #e0e0e0;
}

.edit-profile-icon {
  position: relative;;
  left: 290px;
  bottom: 100px;
  font-size: 19px;
  cursor: pointer;
  width: 70px;
  text-align: center;
  border-radius: 10px;
  background-color: rgb(0, 81, 255);
  color:white;
}

.profile-username-container {
  display: flex;
  flex-direction: column;
}

.profile-username {
  font-size: 24px;
}

.follow-button,
.following-button {
  border: none;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

.follow-button {
  background-color: #0095f6;
  color: white;
}

.following-button {
  background-color: #e0e0e0;
  color: black;
}

.profile-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.profile-stats {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.stat {
  margin-right: 20px;
  text-align: center;
}

.stat-count {
  font-size: 24px;
}

.stat-label {
  font-size: 14px;
}

.profile-bio {
  text-align: center;
  margin-bottom: 10px;
}

.profile-fullname-input {
  width: 200px;
  padding: 4px;
  font-size: 16px;
}

.profile-fullname {
  font-size: 20px;
}

.profile-description {
  font-size: 14px;
  color: gray;
}

.save-profile-button {
  border: none;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: bold;
  background-color: #0095f6;
  color: white;
  cursor: pointer;
}

.profile-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.profile-gallery img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  margin: 10px;
}

.signout-button {
  border: none;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 16px;
  font-weight: bold;
  background-color: #0095f6;
  color: white;
  cursor: pointer;
  margin-top: 20px;
}
.uiverse {
  position: relative;
  background: #ffffff;
  color: #000;
  padding: 15px;
  margin: 10px;
  border-radius: 10px;
  width: 200px;
  height: 50px;
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.tooltip {
  position: absolute;
  top: 0;
  font-size: 14px;
  background: #ffffff;
  color: #ffffff;
  padding: 5px 8px;
  border-radius: 5px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.tooltip::before {
  position: absolute;
  content: "";
  height: 8px;
  width: 8px;
  background: #ffffff;
  bottom: -3px;
  left: 50%;
  transform: translate(-50%) rotate(45deg);
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.uiverse:hover .tooltip {
  top: -45px;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

svg:hover span,
svg:hover .tooltip {
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
}

.uiverse:hover,
.uiverse:hover .tooltip,
.uiverse:hover .tooltip::before {
  background: linear-gradient(320deg, rgb(3, 77, 146), rgb(0, 60, 255));
  color: #ffffff;
}
.loca{
  width: 100%;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  gap: 100px;
  background-color: whitesmoke;
}
