@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400;700&display=swap');

:root {
    --mainColor: #ffffff;

}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}

body {
    font-family: 'Source Sans Pro', sans-serif;

}



.wave {
    position: fixed;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
}

.container {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 7rem;
    padding: 0 2rem;
}

.img {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.img img {
    width: 500px;

}

.login-container {
    display: flex;
    align-items: center;
    text-align: center;
}

form {
    width: 360px;
    text-transform: uppercase;
}

.avatar {
    width: 100px;
}

form h2 {
    font-size: 2.9rem;
    text-transform: uppercase;
    margin: 15px 0;
    color: #333;
}

.input-div {
    position: relative;
    display: grid;
    grid-template-columns: 7% 93%;
    margin: 25px 0;
    padding: 5px 0;
    border-bottom: 2px so lid #d9d9d9;
    text-decoration: none;
    outline: none;


}

.input-div:after,
.input-div:before {
    content: '';
    position: absolute;
    bottom: -2px;
    width: 0;
    height: 2px;
    background-color: var(--mainColor);
    transition: .3s;
    outline: none;
}

.input-div:after {
    right: 50%;
}

.input-div:before {
    left: 50%;
}

.input-div.focus .i i {
    color: var(--mainColor);
}

.input-div.focus div h5 {
    top: -5px;
    font-size: 10px;

}

.input-div.focus:after,
.input-div.focus:before {
    width: 50%;

}

.input-div.one {
    margin-top: 0;
}

.input-div.two {
    margin-bottom: 4px;
}

.i {
    display: flex;
    justify-content: center;
    align-items: center;
}

.i i {
    color: #d9d9d9;
    transition: .3s;
}

.input-div>div {
    position: relative;
    height: 45px;
}

.input-div>div h5 {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: #999;
    font-size: 18px;
    transition: .3s;
}


.input {
    position: absolute;
    outline: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: none;
    background: none;
    padding: 0.5rem 0.7rem;
    font-size: 1.2rem;
    color: #555;
    font-family: 'Source Sans Pro', sans-serif;

}

a {
    display: block;
    text-align: center;
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-size: 0.7rem;
    letter-spacing: 3px;
    transition: .3s;
    margin: 20px;
}

.already {
    color: blue;
    animation:infinite;
    animation-duration: 1s;
    -webkit-animation: alternate-reverse;
    font-weight: 500;
    font-size: 13px;
  }
  

a:hover {
    color: var(--mainColor);
}
