.recipe-details {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
}

.aside {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-right: 20px;
}
.channel{
  display: flex;
  flex-direction: wrap;
  position: relative;
  margin-top: 10%;
  left: 10px;
  background-color: white;
  gap: 30px;
  border: 20px;
  border-color: black;
  border-radius: 30px;
}
.recipe-image {
  width: 400px;
  height: 400px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
}

.recipe-title {
  font-size: 24px;
  margin-bottom: 10px;
}

.recipe-uploader {
  font-size: 16px;
  color: rgb(0, 0, 0);
  margin:0;
}
.channel-row {
  display: flex;
  align-items: center;
  padding: 10px;
  gap: 4px;
  width: 400px;
  background-color: #f8f8f8;
}
.channel-row-2 {
  display: flex;
  align-items: center;
  padding: 10px;
  gap: 4rem;
  width: 400px;
  background-color: #f8f8f8;
}
.channel-logo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.channel-info {
  display: flex;
  flex-direction: wrap;
  align-items: center;
}

.channel-name {
  margin: 0;
  margin-top: 10px;
  font-size: 18px;
}


.follow-button {
  padding: 10px 20px;
  font-size: 15px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 20px;
  margin-top: 6px;
  width: 130px;
  font-weight: 500;

}
.dish{
  background-color: #ffffff;
}

.follow-button:hover {
  background-color: #0056b3;
}

.content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.section-title {
  font-size: 30px;
  margin-bottom: 10px;
}

.ingredient-list,
.instruction-list {
  text-align: left;
  margin-left: 20px;
  margin-bottom: 20px;
}

li {
  margin-bottom: 5px;
  gap: 10px;
}
[id='toggle-heart'] {
  display: none;
}

[id='toggle-heart']:checked + label {
  color: #e2264d;
  filter: none;
  will-change: font-size;
  animation: heart 1s cubic-bezier(0.17, 0.89, 0.32, 1.49);
}

[id='toggle-heart']:checked + label:before, [id='toggle-heart']:checked + label:after {
  animation: inherit;
  animation-timing-function: ease-out;
}

[id='toggle-heart']:checked + label:before {
  will-change: transform, border-width, border-color;
  animation-name: bubble;
}

[id='toggle-heart']:checked + label:after {
  will-change: opacity, box-shadow;
  animation-name: sparkles;
}

[id='toggle-heart']:focus + label {
  text-shadow: 0 0 3px white, 0 1px 1px white, 0 -1px 1px white, 1px 0 1px white, -1px 0 1px white;
}

[for='toggle-heart'] {
  align-self: center;
  position: relative;
  color: #888;
  font-size: 2em;
  filter: grayscale(1);
  user-select: none;
  cursor: pointer;
}

[for='toggle-heart']:before, [for='toggle-heart']:after {
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  content: '';
}

